html {
    font-size: 100%;
    box-sizing: border-box;
}

// for all elements, inherit from html
*, *::before, *::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background: var(--dark-bg);
    color: var(--dark-text2);
}