@use '../util/' as *;

.header{
    
    display: flex;
    flex-wrap: wrap;
    margin-top: rem(40);

    @include breakpoint(medium){
        display: flex;
        justify-content: space-between;
    }

    &__title{
        width: 100%;

        @include breakpoint(medium){
            width: auto;
        }
    }
    
    &__subtitle{
        font-size: rem(14);
        font-weight: 700;
        color: var(--dark-text1);
    }

    &__toggle{
    }
}